import MyMask from '../../common/myMask.vue';
import {publicing,myPOST} from '../../api/api.js';
import {addAddress} from '../../api/request.js';
import {addressEneity} from '../../api/vo/eneity.js';
export default {
	components:{MyMask},
	data() {
		return {
			show: false,
			maskShow:false,
			// addressMessage:{
			// 	name:'',
			// 	phone:'',
			// 	university:'',
			// 	university_id:'',
			// 	address:''
			// },
			dataView:addressEneity,
			errorType: ['message'],
			rules: {
				name: [
					{
						required: true,
						message: '请输入姓名',
						trigger: 'blur'
					},
					{
						min: 2,
						max: 15,
						message: '姓名长度在2到15个字符',
						trigger: ['change', 'blur']
					},
					{
						validator: (rule, value, callback) => {
							// 调用uView自带的js验证规则，详见：https://www.uviewui.com/js/test.html
							return this.$u.test.chinese(value);
						},
						message: '姓名必须为中文',
						// 触发器可以同时用blur和change，二者之间用英文逗号隔开
						trigger: ['change', 'blur']
					}
				],
				phone: [
					{
						required: true,
						message: '请输入手机号',
						trigger: ['change', 'blur']
					},
					{
						validator: (rule, value, callback) => {
							// 调用uView自带的js验证规则，详见：https://www.uviewui.com/js/test.html
							return this.$u.test.mobile(value);
						},
						message: '手机号码不正确',
						// 触发器可以同时用blur和change，二者之间用英文逗号隔开
						trigger: ['change', 'blur']
					}
				]
			},
		}
	},
	computed:{
		borderCurrent() {
			return this.border ? 0 : 1;
		},
		university(){
			if(this.$store.state.university)
			{
				this.university_id=this.$store.state.university._id;
				return this.$store.state.university.name;
			}
			else
			{
				return "请选择定位大学"
			}
		}
	},
	methods: {
		setDefault() {},
		showRegionPicker() {
			uni.navigateTo({
				url: '../nearTheSchool/nearTheSchool'
			});
		},
		//  提交表单
		submit() {
			//增加地址并回到上一级的路由
			console.log('提交信息');
			
			

			 if(!this.$store.state.university.name){
				 this.$u.toast("请选择学校!");
				 return;
			 }
			 let userInfo = uni.getStorageSync("userInfo")
			 this.dataView.user_id =userInfo._id;
			 this.dataView.university_id = this.$store.state.university._id
			//进行学校的选择以及是否登陆检测
			
			console.log(this.dataView);
			this.maskShow=true;
			myPOST(addAddress,this.dataView)
			.then((res)=>{
				this.maskShow=false;
				console.log(res.errMsg)
				if(res.errMsg=="collection.add:ok"){
					this.$u.toast("增加成功!");
					uni.navigateBack({
						
					})
				}
				else{
						this.$u.toast("增加失败!");
					}
			})
			.catch((err)=>{
				this.maskShow=false;
				this.$u.toast("增加失败!");
			})
			
		}
	
	},
	onReady() {
		this.$refs.uForm.setRules(this.rules);
	}
};
